<template>
    <div class="orderDetails" :class="{themeLBox:bindThemeL}">
        <navigation-bar @onLeftClick="goToBack" :pageName="title" ref="flexFull"></navigation-bar>
        <div class="post-wrapper" ref="postWrapper">
            <div class="post-content">
                <div class="pinkBox" v-if="parentData.flag== 1&&notClick">
                    <div>
                        <div class="refundInfo">
                            <div class="refundTxt">待付款</div>
                        </div>
                        <div class="refundTime">还剩{{payCountDown}}，将自动取消订单</div>
                    </div>
                    <img src="@/assets/images/mall/shoppingCart_img_waitForPayment.png" alt="">
                </div>
                <div class="pinkBox" v-if="parentData.flag== 1&&!notClick">
                    <div>
                        <div class="refundInfo">
                            <div class="refundTxt">已取消</div>
                        </div>
                        <div class="refundTime">订单已经取消</div>
                    </div>
                    <img src="@/assets/images/mall/shoppingCart_img_cancel.png" alt="">
                </div>
                <div class="pinkBox" v-if="parentData.flag== 3&&notClick">
                    <div>
                        <div class="refundInfo">
                            <div class="refundTxt">待收货</div>
                        </div>
                        <div class="refundTime">还剩{{payCountDown}}，将自动确认收货</div>
                    </div>
                    <img src="@/assets/images/mall/shoppingCart_img_waitForGoods.png" alt="">
                </div>
                <div class="pinkBox" v-if="parentData.flag== 7&&notClick">
                    <div>
                        <div class="refundInfo">
                            <div class="refundTxt">待发货</div>
                        </div>
                        <div class="refundTime">商户正在为您备货中</div>
                    </div>
                    <img src="@/assets/images/mall/shoppingCart_img_waitForGoods.png" alt="">
                </div>
                <div class="pinkBox" v-if="parentData.flag== 3&&!notClick">
                    <div>
                        <div class="refundInfo">
                            <div class="refundTxt">已收货</div>
                        </div>
                        <div class="refundTime">订单已签收</div>
                    </div>
                    <img src="@/assets/images/mall/shoppingCart_img_waitForGoods.png" alt="">
                </div>
                <div class="pinkBox" v-if="(parentData.flag== 7&&!notClick)||parentData.flag== 8">
                    <div>
                        <div class="refundInfo">
                            <div class="refundTxt">等待退款</div>
                        </div>
                        <div class="refundTime">商户同意后，退款金额将原路返回</div>
                    </div>
                    <img src="@/assets/images/mall/shoppingCart_img_waitForGoods.png" alt="">
                </div>
                <div class="pinkBox" v-if="parentData.flag== 9">
                    <div>
                        <div class="refundInfo">
                            <div class="refundTxt">退款成功</div>
                        </div>
                        <div class="refundTime">{{OrderDetailData.refundTime}}</div>
                    </div>
                    <img src="@/assets/images/mall/shoppingCart_img_waitForGoods.png" alt="">
                </div>
                <div class="pinkBox" v-if="parentData.flag== 4">
                    <div>
                        <div class="refundInfo">
                            <div class="refundTxt">已完成</div>
                        </div>
                        <div class="refundTime">恭喜您已完成，欢迎继续下单</div>
                    </div>
                    <img src="@/assets/images/mall/shoppingCart_img_done.png" alt="">
                </div>
                <div class="pinkBox" v-if="parentData.flag== 5">
                    <div>
                        <div class="refundInfo">
                            <div class="refundTxt">待评价</div>
                        </div>
                        <div class="refundTime">您还没有对该商品进行评价哦</div>
                    </div>
                    <img src="@/assets/images/mall/shoppingCart_img_waitForComments.png" alt="">
                </div>
                <div class="pinkBox" v-if="parentData.flag== 6">
                    <div>
                        <div class="refundInfo">
                            <div class="refundTxt">已取消</div>
                        </div>
                        <div class="refundTime">当前订单已取消</div>
                    </div>
                    <img src="@/assets/images/mall/shoppingCart_img_cancel.png" alt="">
                </div>
                <div class="detailsBox">
                    <!--物流轨迹-->
                    <div class="expressDetaile"
                        v-if="OrderDetailData.shopList[0].expressCode=='zps'"
                        @click="showActionBox">
                        <img class="addressIcon" src="@/assets/images/mall/express/icon_Logistics_cut.png" alt="">
                        <div class="addressBox">
                            <div class="addressInfoBox">
                                <div class="addressName">本订单由商家自行配送</div>
                            </div>
                            <div class="addressTxt">商家电话：<span class="phoneNum" @click.stop="callPhone(OrderDetailData.shopList[0].materialFlow.shopPhone)">{{OrderDetailData.shopList[0].materialFlow.shopPhone}}</span></div>
                        </div>
                        <div class="expressMsg">商家配送</div>
                        <img class="titleImg" :src="imgIcon.titleImg1" alt="">
                        <img class="right-arror-img" :src="rightArrowImg" alt="">
                        <img class="lineDiv" :src="lineImg" alt="">
                    </div>
                    <div class="expressDetaile"
                         v-if="OrderDetailData.shopList[0].expressCode=='other'"
                         @click="showActionBox(false)">
                        <img class="addressIcon" src="@/assets/images/mall/express/icon_Logistics_cut.png" alt="">
                        <div class="addressBox">
                            <div class="addressInfoBox">
                                <div class="addressName addressName1" >物流单号：{{OrderDetailData.shopList[0].materialFlow.expressTrackNo}}
                                    <img style="width: 13px;height: 13px;margin-left: 5px" src="../../assets/images/newSlicing/copy_cut.png" alt=""
                                         @click.stop="copyBtn(OrderDetailData.shopList[0].materialFlow.expressTrackNo)"></div>
                            </div>
                            <div class="addressTxt">物流公司：{{OrderDetailData.shopList[0].materialFlow.expressCompany}}</div>
                            <div class="addressTxt">商家电话：<span class="phoneNum" @click.stop="callPhone(OrderDetailData.shopList[0].materialFlow.shopPhone)">{{OrderDetailData.shopList[0].materialFlow.shopPhone}}</span></div>
                        </div>
                        <div class="expressMsg">物流配送</div>
                        <img class="titleImg" :src="imgIcon.titleImg2" alt="">
                        <img class="right-arror-img" :src="rightArrowImg" alt="">
                        <img class="lineDiv" :src="lineImg" alt="">
                    </div>
                    <div class="expressDetaile"
                         v-if="OrderDetailData.shopList[0].expressCode=='dada'"
                         @click="gotoMapforExpress">
                        <img class="addressIcon" src="@/assets/images/mall/express/icon_Logistics_cut.png" alt="">
                        <div class="addressBox">
                            <div class="addressInfoBox">
                                <div class="addressName">{{expressStatusMsgInFirstDiv}}</div>
                            </div>
                            <div class="addressTxt">{{serviceMsg}}</div>
                        </div>
                        <div class="expressMsg">物流配送</div>
                        <img class="titleImg" :src="imgIcon.titleImg2" alt="">
                        <img class="right-arror-img" :src="rightArrowImg" alt="">
                        <img class="lineDiv" :src="lineImg" alt="">
                    </div>
                    <div class="expressDetaile"
                         v-if="OrderDetailData.shopList[0].expressCode=='shunfeng'"
                         @click="gotoMapforExpress">
                        <img class="addressIcon" src="@/assets/images/mall/express/icon_Logistics_cut.png" alt="">
                        <div class="addressBox">
                            <div class="addressInfoBox">
                                <div class="addressName">{{expressStatusMsgInFirstDiv}}</div>
                            </div>
                            <div class="addressTxt">{{OrderDetailData.shopList[0].materialFlow.orderRouteInfoList[0].routeMessage}}</div>
                        </div>
                        <div class="expressMsg">物流配送</div>
                        <img class="titleImg" :src="imgIcon.titleImg2" alt="">
                        <img class="right-arror-img" :src="rightArrowImg" alt="">
                        <img class="lineDiv" :src="lineImg" alt="">
                    </div>
                    <!--收货地址-->
                    <div class="addressInfo">
                        <img class="addressIcon" src="@/assets/images/mall/address.png" alt="">
                        <div class="addressBox">
                            <div class="addressInfoBox">
                                <div class="addressName" v-if="OrderDetailData.deliveryType==2">
                                    {{OrderDetailData.pickupName}}</div>
                                <div class="addressName" v-if="OrderDetailData.deliveryType!=2">
                                    {{OrderDetailData.recName}}</div>
                                <div class="addPhone" v-if="OrderDetailData.deliveryType==2">
                                    {{OrderDetailData.pickupPhone}}</div>
                                <div class="addPhone" v-if="OrderDetailData.deliveryType!=2">
                                    {{OrderDetailData.recPhone}}</div>
                            </div>
                            <div class="addressTxt">
                                地址：{{OrderDetailData.recAddress}}
                            </div>
                        </div>
                    </div>
                    <!--店铺信息-->
                    <div class="goodsItem" v-for="(shopItem,shopIndex) in OrderDetailData.shopList"
                        :key="'shopIndex'+shopIndex">
                        <div class="shopInfoBox">
                            <div class="merchantInfo merchantInfoBox">
                                <div class="merchantBox" @click="goToShopPage(shopItem.shopCode)">
                                    <div class="merchantName">{{shopItem.shopName}}</div>
                                    <img src="@/assets/images//mall/mall_icon_more.png" alt="">
                                </div>
                                <div class="stateTxt" v-if="parentData.flag== 1&&notClick">等待付款</div>
                                <div class="stateTxt" v-if="parentData.flag== 1&&!notClick">已取消</div>
                                <div class="stateTxt" v-if="parentData.flag== 3&&notClick">配送中</div>
                                <div class="stateTxt" v-if="parentData.flag== 7&&notClick">备货中</div>
                                <div class="stateTxt" v-if="(parentData.flag== 7&&!notClick)||parentData.flag== 8">退款中
                                </div>
                                <div class="stateTxt" v-if="parentData.flag== 9">退款成功</div>
                                <div class="stateTxt" v-if="parentData.flag== 3&&!notClick">订单签收</div>
                                <div class="stateTxt" v-if="parentData.flag== 4">交易成功</div>
                                <div class="stateTxt" v-if="parentData.flag== 5">等待评价</div>
                            </div>
                            <div v-show="shopItem.deliveryDate">

                                <!-- <div class="deliverStatus" v-show="shopItem.deliveryDate"> -->
                                <div class="deliverTimeTitle">送达时间：{{shopItem.deliveryDate}}
                                    {{shopItem.deliveryTimeStart}}-{{shopItem.deliveryTimeEnd}}</div>
                                <!-- <div class="StatusTxt" style="color: #333;">{{shopItem.deliveryDate}} {{shopItem.deliveryTimeStart}}-{{shopItem.deliveryTimeEnd}}</div> -->
                                <!-- </div> -->
                            </div>
                        </div>

                        <div class="nomalLine"></div>
                        <div v-for="(skuItem,skuIndex) in shopItem.skuList" :key="'skuIndex'+skuIndex">
                            <div class="goodsItemBox" v-if="skuIndex<showMoreSku">
                                <div class="goodsInfo" :class="parentData.flag== 3&&notClick?'goodsInfoBox':''"
                                    @click="goToGoodsDetails(skuItem,skuItem.activitySpuId)">
                                    <img class="goodsImg" :src="skuItem.spuPicUrl" alt="">
                                    <div class="goodsInfoTxt">
                                        <div class="classify">
                                            <div class="goodsName">{{skuItem.spuName}}</div>
                                            <span class="moneyNum">￥{{skuItem.skuPrice}}</span>
                                        </div>
                                        <div class="classify">
                                            <div class="specificationInfo">
                                                <span class="specificationName">{{skuItem.attrValueContent}}</span>
                                                <span class="specificationNum">x{{skuItem.skuCount}}</span>
                                            </div>
                                            <div class="afterSaleBtn"  v-if="parentData.flag == 5||parentData.flag == 4" @click.stop="applyForAfterBtn(shopItem,skuItem)">申请售后</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="goodsMoreBottom" v-if="shopItem.skuList.length>3" @click="showMore()">
                            <div class="bottomTitle" v-if="showMoreSku==3">共{{shopItem.skuList.length}}件商品</div>
                            <div class="bottomTitle" v-if="showMoreSku!=3">收起</div>
                            <img class="iconImg" :src="showMoreSku!=3?upIcon:downIcon" alt="">
                        </div>
                        <div class="remark" v-for="(actItem,i) in shopItem.actList" :key="'actItem'+i">
                            <div class="reductionBox">
                                <div class="reductionTag reductionTagGreen"
                                    v-if="actItem.actType==0">满减</div>
                                <div class="reductionTag reductionTagRed" v-if="actItem.actType==1">打包</div>
                                <div class="deduction_name" v-if="actItem.actType==0">
                                    满{{actItem.ruleFull}}减{{actItem.ruleReduction}}</div>
                                <div class="deduction_name" v-if="actItem.actType==1">
                                    {{actItem.rulePrice}}元{{actItem.ruleAmount}}件</div>
                            </div>
                            <div class="deduction_num">
                                -¥{{actItem.actTotal}}</div>
                        </div>
                        <div class="remark" v-if="shopItem.couponTotal">
                            <div class="reductionBox">
                                <div class="reductionTag reductionTagOrange">优惠券</div>
                                <div class="deduction_name">
                                    已使用推荐优惠券</div>
                            </div>
                            <div class="deduction_num">
                                -¥{{shopItem.couponTotal}}</div>
                        </div>
                        <div class="remark">
                            <div class="remarkName">包装费</div>
                            <div class="deliveryNum">
                                ¥{{shopItem.packFee}}</div>
                        </div>
                        <div class="remark">
                            <div class="remarkName">配送费</div>
                            <div class="deliveryNum">
                                ¥{{shopItem.delivery}}</div>
                        </div>
                        <div class="remark">
                            <div class="remarkName">备注</div>
                            <div class="deliveryNum">
                                {{shopItem.remark}}</div>
                        </div>
                    </div>
                    <!--订单信息-->
                    <div class="detailsInfo">
                        <div class="detailsItem">
                            <span class="detailsLeft">订单编号</span>
                            <div class="orderInfo">
                                <div class="orderNum">{{OrderDetailData.billNo}}</div>
                                <button class="orderCopy" @click.stop="copyBtn(OrderDetailData.billNo)">复制</button>
                            </div>
                        </div>
                        <div class="detailsItem" v-if="parentData.flag!= 1&&parentData.flag!= 6">
                            <div class="detailsLeft">支付方式</div>
                            <div class="detailsRight">{{OrderDetailData.payType}}</div>
                        </div>
                        <div class="detailsItem" v-if="parentData.flag!= 1&&parentData.flag!= 6">
                            <div class="detailsLeft">支付时间</div>
                            <div class="detailsRight">{{OrderDetailData.payTime}}</div>
                        </div>
                        <div class="detailsItem">
                            <div class="detailsLeft">创建时间</div>
                            <div class="detailsRight">{{OrderDetailData.createdTime}}</div>
                        </div>
                        <div class="detailsItem">
                            <div class="detailsLeft">可获积分</div>
                            <div class="detailsRight">+ {{OrderDetailData.pointIncome}}</div>
                        </div>
                    </div>
                    <!--优惠信息-->
                    <div class="detailsInfo discountsInfo">
                        <div class="detailsItem">
                            <div class="detailsLeft">商品总额</div>
                            <div class="detailsRight" v-if="orderDataIsReady">
                                ￥{{Number(OrderDetailData.billTotal).toFixed(2)}}</div>
                        </div>
                        <div class="detailsItem">
                            <div class="detailsLeft">商品优惠</div>
                            <div class="detailsRight" v-if="orderDataIsReady">
                                -￥{{Number(OrderDetailData.actTotal).toFixed(2)}}</div>
                        </div>
                        <div class="detailsItem" v-show="OrderDetailData.billType2==0">
                            <div class="detailsLeft">商家优惠券</div>
                            <div class="detailsRight" v-if="orderDataIsReady">
                                -￥{{Number(OrderDetailData.couponTotal).toFixed(2)}}</div>
                        </div>
                        <div class="detailsItem" v-show="OrderDetailData.billType2==0">
                            <div class="detailsLeft">平台红包优惠</div>
                            <div class="detailsRight" v-if="orderDataIsReady">
                                -￥{{Number(OrderDetailData.couponPlatformTotal).toFixed(2)}}</div>
                        </div>
                        <div class="detailsItem" v-show="OrderDetailData.billType2==0">
                            <div class="detailsLeft">积分抵扣</div>
                            <div class="detailsRight" v-if="orderDataIsReady">
                                -￥{{(Number(OrderDetailData.pointCost / 100)).toFixed(2)}}</div>
                        </div>
                        <div class="detailsItem">
                            <div class="detailsLeft">包装费</div>
                            <div class="detailsRight" v-if="orderDataIsReady">
                                +￥{{Number(OrderDetailData.packTotal).toFixed(2)}}</div>
                        </div>
                        <div class="detailsItem">
                            <div class="detailsLeft">配送费</div>
                            <div class="detailsRight" v-if="orderDataIsReady">
                                +￥{{Number(OrderDetailData.deliveryTotal).toFixed(2)}}</div>
                        </div>
                        <div class="line"></div>
                        <div class="detailsMoney">
                            <div class="detailsLeft">实付金额：</div>
                            <div class="detailsRight">
                                <span>￥</span>
                                <span class="pocketNum"
                                    v-if="orderDataIsReady">{{Number(OrderDetailData.realPayTotal).toFixed(2)}}</span>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="fixedHeight" :style="{height:50+44+this.$footBarHeight+'px'}"></div>
            </div>

        </div>
        <div class="fixedBox"
            v-if="(notClick||parentData.flag != 1)"
            :style="{paddingBottom:this.$footBarHeight+'px'}">
            <div class="relationBtn" v-if="OrderDetailData.shopList.length==1" @click="callShop">
                <img class="relationImg" src="@/assets/images/mall/icon_customerservice_cut.png" alt="">
                <div class="relationTxt">客服</div>
            </div>
            <div class="relationBtn" v-if="OrderDetailData.shopList.length!=1">

            </div>
            <div class="fixedBtnList" v-if="parentData.flag == 1&&notClick">
                <div class="removeBtn" @click="orderCancel()">取消订单</div>
                <div class="gotopayBtn" @click="prepareToPayOrder()">去支付</div>
            </div>
            <div class="fixedBtnList" v-if="parentData.flag == 3">
                <div class="evaluateBtn" @click="goToProduct(1)">再次购买</div>
                <div class="gotopayBtn" @click="confirmOrder()" v-show="notClick">确认收货</div>
            </div>
            <div class="fixedBtnList" v-if="parentData.flag == 7&&notClick">
                <div class="removeBtn" @click="refundBySub()">申请退款</div>
                <div class="evaluateBtn" @click="goToProduct(1)">再次购买</div>
            </div>
            <div class="fixedBtnList" v-if="parentData.flag == 8||(parentData.flag == 7&&!notClick)||parentData.flag == 9||parentData.flag == 6">
                <div class="evaluateBtn" @click="goToProduct(1)">再次购买</div>
            </div>
            <div class="fixedBtnList" v-if="parentData.flag == 4">
                <div class="evaluateBtn" @click="goToProduct(1)" v-show="OrderDetailData.billType2==0">再次购买</div>
            </div>
            <div class="fixedBtnList" v-if="parentData.flag == 5">
                <div class="evaluateBtn" @click="goToProduct(2)" v-show="OrderDetailData.billType2==0">再次购买</div>
                <div class="gotopayBtn" @click="goToMyEvaluation()">评价送积分</div>
            </div>
        </div>
        <van-popup v-model="payWindowShow" position="bottom" class="vanPopup" @close="closePopup">
            <div class="payModeBox">
                <div class="payModeTitle">支付方式</div>
                <div v-show="isAcppay" class="payModeItem" @click="alipay(2)">
                    <div class="unipayclass">
                        <img class="unipayIcon" src="@/assets/images/common/icon_unipay.png" alt="">
                        <div style="display: inline-block; vertical-align:middle;">云闪付</div>
                        <img class="iconUnipayRight" src="@/assets/images/common/icon_unipay_right.png" alt=""
                            style="display: inline-block;vertical-align:middle;width: 21px;height: 21px;">
                        <div class="saleIcon" style="display: inline-block;vertical-align:middle;" v-if="isAcpPayTag">
                            {{acpPayTag}}</div>
                    </div>
                </div>
                <div v-show="isAlipay" class="payModeItem" @click="alipay(1)">
                    <img src="@/assets/images/common/icon_alipay.png" alt="">
                    <div>支付宝</div>
                </div>
<!--                <div v-show="isWechart" class="payModeItem" @click="alipay(0)">-->
<!--                    <img src="@/assets/images/common/icon_wechart.png" alt="">-->
<!--                    <div>微信</div>-->
<!--                </div>-->
            </div>
            <div class="cancelBtn" @click="closePopup">取消</div>
        </van-popup>
        <van-action-sheet v-model="isShowAction" :title="isShowTitle==0?'商家配送说明':'物流跟踪'">
            <div class="actionContent" v-if="isShowTitle==1">
                <div>物流单号：{{OrderDetailData.shopList[0].materialFlow.expressTrackNo}}
                    <img style="width: 13px;height: 13px;margin-left: 5px" src="../../assets/images/newSlicing/copy_cut.png" alt=""
                         @click.stop="copyBtn(OrderDetailData.shopList[0].materialFlow.expressTrackNo)"></div>
                <div>物流公司：{{OrderDetailData.shopList[0].materialFlow.expressCompany}}</div>
                <div  @click.stop="callPhone(OrderDetailData.shopList[0].materialFlow.shopPhone)">商家电话：<span style="color: #4A90E2;">{{OrderDetailData.shopList[0].materialFlow.shopPhone}}</span></div>
            </div>
            <div class="actionContent" v-if="isShowTitle==0">
                <div v-if="OrderDetailData.shopList[0].materialFlow">{{OrderDetailData.shopList[0].materialFlow.accountFor}}</div>
            </div>
        </van-action-sheet>
        <!--打电话弹窗-->
        <van-popup v-model="isShowPhoneSelect" position="bottom" class="vanPopup" @close="onCancel">
            <div class="phoneSelectBox">
                <div v-if="OrderDetailData" class="phoneSelect" @click="callProperty(OrderDetailData.shopList[0].shopPhone)">
                    <div>商家电话：</div>
                    <div class="phoneNum">{{OrderDetailData.shopList[0].shopPhone}}</div>
                </div>
                <div v-if="OrderDetailData" class="phoneSelect" @click="callProperty(OrderDetailData.serviceTel)">
                    <div>平台客服：</div>
                    <div class="phoneNum">{{OrderDetailData.serviceTel}}</div>
                </div>
            </div>
            <div class="cancelPhoneBtn" @click="onCancel">取消</div>
        </van-popup>
        <serviceItem :serviceInfo="serviceInfo" @setServiceInfo="setServiceInfo"></serviceItem>

        <div class="blackImgBox" v-show="isShowBlankImg" :style="{top:flexFullHeight+'px'}">
            <img src="@/assets/images/themeImg/order_details.png" alt="">
        </div>
    </div>
</template>

<script>
    import { getOrderDetailPlanBService, getEvaluateOrderDetailService, orderCancelService, confirmOrderService, refundBySubService, batchAddGoodsService, deleteOrderService, exchangeIdService } from "../../services/goodsService";
    import { prepareToPayOrderService, orderDetailService, unionOrderPrepayV34Service } from '../../services/productOrderService.js';
    import { applyForAfter} from "../../services/orderListService";
    import { mapActions, mapState } from "vuex";
    import NavigationBar from '@/common/currency/NavigationBar.vue';
    import { Toast, Dialog,ActionSheet  } from "vant";
    import upIcon from "@/assets/images/mall/mall_icon_up.png";
    import downIcon from "@/assets/images/mall/mall_icon_down.png";
    import { storage } from '../../utils/storage';
    import Config from "../../common/config";
    import serviceItem from "../superiorProduct/serviceItem"

    export default {
        name: "orderDetails",
        data() {
            return {
                isShowBlankImg:true,
                flexFullHeight:'',
                isShowAction:false,
                isShowPhoneSelect:false,
                isShowTitle:-1,
                /*大字模式*/
