                let _this = this;
                let newIndex=index;
                if (index==0){
                    newIndex=1;
                }else if (index==1){
                    newIndex=0;
                }
                let pram = {
                    'merOrderId': this.payParam.merOrderId,
                    'billNo':this.parentData.billNo,
                    'payType': newIndex,
                    'channelId':storage.getStorage("channelId", false) !=undefined ? storage.getStorage("channelId", false):null

                }
                unionOrderPrepayV34Service(pram).then(res => {
                    this.closePopup(true);
                    if (res.resultCode !== "0") {
                        Dialog.alert({
                            message: res.msg
                        }).then(() => {
                        });
                        return false;
                    }
                    if (index ===2){
                        this.closePopup(true);
                        let pid = 666;
                        let param = {};
                        param.tn = res.data.appPayRequest.tn;
                        param.payMode = this.payParam.acpGatewayType;
                        let paramsData={
                            data:{
                                'payData':JSON.stringify(param),
                                'schemesUrl':''},
                            'encoder':'base64',
                            'payType':'UnionPay'
                        };
                        if (this.$devicePlatform =="ios"){
                            if (window.webkit && window.webkit.messageHandlers && window.webkit.messageHandlers.JSCtrl){
                                window.webkit.messageHandlers.JSCtrl.postMessage(['request_pay_online',pid,paramsData]);
                            }else {
                                window.webkit.messageHandlers.unifypay.request_pay_online({
                                    'pid':666,
                                    'paramsData':paramsData
                                })
                            }
                        }else if (this.$devicePlatform == "android"){
                            window.store.request_pay_online(pid,JSON.stringify(paramsData));
                        }
                    }else {
                        this.payParam.payWay = index;
                        //监听ios支付结果 检测当前订单状态
                        this.closePopup(true);
                        let pid = 666;
                        console.log(JSON.stringify(res.data.appPayRequest));
                        let paramsData={
                            data:{
                                'payData':JSON.stringify(res.data.appPayRequest),
                                'schemesUrl':''},
                            'encoder':'base64'
                        };
                        console.log(paramsData);
                        if (index==1){
                            paramsData.payType='Alipay';
                        }else if(index==0) {
                            paramsData.payType='WeChatPay';
                        }
                        if (this.$devicePlatform =="ios"){
                            if (window.webkit && window.webkit.messageHandlers && window.webkit.messageHandlers.JSCtrl){
