                                })
                            }
                        }else if (this.$devicePlatform == "android"){
                            window.store.request_pay_online(pid,JSON.stringify(paramsData));
                        }
                    }
                }).catch(error => {
                    this.closePopup(true);
                    Dialog.alert({
                        message: '网络异常，请稍后重试！'
