                    } else {
                        Toast(res.msg)
                    }
                }).catch(error => {
                });
            },
            //删除该订单
            deleteOrder() {
                let paramData = {
                    subBillNo: this.parentData.subBillNo,
                    ownerCode: this.$userInfo.OWNER_CODE
                }
                deleteOrderService(paramData).then(res => {
                    if (res.resultCode == 0) {
                        Toast('删除成功');
                        this.notClick = false;
                        this.goToBack();
                    }
                }).catch(error => {
                });
            },
            //打电话
            callProperty(phoneNum) {
                this.onCancel();
                if (this.$deciceType=='phone'){
                    window.location.href = "tel:" + phoneNum;
                }
            },
            //打电话
            callPhone(phoneNum) {
                // window.location.href = "tel:" + this.OrderDetailData.shopList[0].shopPhone;
                const platform = navigator.userAgent;
                const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
                if (isiOS) {
                    setTimeout(
                        function(){
                            window.location.href = "tel:" + phoneNum;
                        }, 0);
                } else {
                    Dialog.confirm({
                        title: "拨打电话",
                        message: phoneNum,
                    }).then(() => {
                        if (this.$deciceType=='phone'){
                            window.location.href = "tel:" + phoneNum;
                        }
                    }).catch(() => {
                        // on cancel
                        Dialog.close;
                    });
                }
            },
            //收起按钮
            showMore() {
                if (this.showMoreSku == 3) {
                    this.showMoreSku = 1000;
                } else {
                    this.showMoreSku = 3;
                }
            },
            //去店铺首页
            goToShopPage(shopCode) {
                this.$router.push({
                    name: 'shopPage',
                    params: {
                        shopCode: shopCode
                    }
                })

            },
            //去商品页
            goToGoodsDetails(skuItem, activitySpuId) {
                if (!activitySpuId) {
                    this.$router.push({
                        name: 'goodsDetail',
                        params: {
                            spuCode: skuItem.spuCode,
                            goodsUrl: skuItem.spuPicUrl
                        }
                    })
                } else {
                    console.log(activitySpuId)
                    let paramData = {
                        activitySpuId: activitySpuId
                    }
                    exchangeIdService(paramData).then(res => {
                        if (res.resultCode == 0) {
                            if (res.data.activitySpuId) {
                                this.$router.push({
                                    name: 'flashSaleGoods',
                                    query: {
                                        spuCode: skuItem.spuCode,
                                        activitySpuId: res.data.activitySpuId,
                                        status: res.data.activityStatus,
                                        goodsUrl: skuItem.spuPicUrl
                                    }
                                })
                            } else {
                                this.$router.push({
                                    name: 'goodsDetail',
                                    params: {
                                        spuCode: skuItem.spuCode
                                    }
                                })
                            }
                        }
                    }).catch(error => {
                    });
                }

            },
            resolveTrackData(trackData) {
                switch (trackData.statusCode) {
                    case "1":
                        this.expressStatusMsgInFirstDiv = '等待配送员接单';
                        this.serviceMsg = '正在为您紧急召唤配送员';
                        break;
                    case "2":
                        this.expressStatusMsgInFirstDiv = '配送员已接单';
                        this.serviceMsg = '配送员正在赶往商家';
                        break;
                    case "3":
                        this.expressStatusMsgInFirstDiv = '正在配送中';
                        this.serviceMsg = '开始为您配送，请耐心等待';
                        break;
                    case "4":
                        this.expressStatusMsgInFirstDiv = '配送已完成';
                        this.serviceMsg = '本次配送已完成，感谢您的信任，欢迎再次选购！';
                        break;
                    case "5":
                        this.expressStatusMsgInFirstDiv = '等待配送员接单';
                        this.serviceMsg = '正在为您紧急召唤配送员';
                        break;
                    case "9":
                        this.expressStatusMsgInFirstDiv = '配送异常';
                        this.serviceMsg = '配送员无法与您取得联系，正在将商品返回商家';
                        break;
                    case "10":
                        this.expressStatusMsgInFirstDiv = '配送异常';
                        this.serviceMsg = '配送员无法与您取得联系，商品已返回商家';
                        break;
                    case "100":
                        this.expressStatusMsgInFirstDiv = '配送员已到店';
                        this.serviceMsg = '配送员已到店，正在取货中';
                        break;
                    default:
                        break;
                }
            },
            resolveotherTrackData(trackData) {
                this.serviceMsg = trackData.routeMessage;
                switch (trackData.expressCode) {
                    case "0":
                        this.expressStatusMsgInFirstDiv = '已发货';
                        break;
                    case "1":
                        this.expressStatusMsgInFirstDiv = '已揽收';
                        break;
                    case "2":
                        this.expressStatusMsgInFirstDiv = '运输中';
                        break;
                    case "3":
                        this.expressStatusMsgInFirstDiv = '派送中';
                        break;
                    case "4":
                        this.expressStatusMsgInFirstDiv = '已签收';
                        break;
                    case "33": case "70":
                        this.expressStatusMsgInFirstDiv = '配送异常';
                        this.serviceMsg = '配送员无法与您取得联系';
                        break;
                    default:
                        break;
                }
            },
            //查看物流信息
            gotoMapforExpress() {
                // '314378699124482048C2',this.$userInfo.AREA_CODE
                this.$router.push({ name: "mapForExpress", params: { subBillNo: this.subBillNo, expressCompany: this.expressCompany } });
            },
            //返回上一页
            goToBack() {
                this.$router.go(-1);
            }
        },
        beforeRouteLeave(to, form, next) {
            this.postWrapperScrollTop = this.$refs.postWrapper.scrollTop;
            next();
        }
    }
</script>

